import React, { useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";

import Donuts from "../../images/donuts-main-baner.jpg";

import "../../styles/Mainsection.scss";

const Mainsection = () => {
  gsap.registerPlugin(ScrollTrigger);
  const donutsBg = useRef(null);

  useEffect(() => {
    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: ".welcome",
        start: "top top",
        end: "bottom top",
        scrub: true,
      },
    });

    tl.to(donutsBg.current, { y: 280 }, 0);
  }, []);

  return (
    <section className="mainSection">
      <h1 className="mainSection__title">
        <span>Rƒôcznie robione</span>Rzemie≈õlnicze pƒÖczki
        <Link to="/produkty">
          <button className="mainSection__title__button">
            Zobacz wszystkie
          </button>
        </Link>
      </h1>
      <img
        src={Donuts}
        alt="Obrazek w tle przedstawiajƒÖcy donuty"
        className="mainSection__background"
        ref={donutsBg}
      />
    </section>
  );
};

export default Mainsection;
